<template>
  <project-anchor :website="project.website">
    <div class="tile__wrapper" :class="classObj" :style="wrapperStyle">
      <project-banner :size="size" :logo="project.bannerLogo" :banner="project.bannerImg" />

      <h3 class="tile__title" :style="titleStyle">{{ project.name }}</h3>
      <p v-if="size != 'sm'" class="tile__description">{{ project.description }}</p>
    </div>
  </project-anchor>
</template>


<script>
import ProjectAnchor from './project-anchor.vue';
import ProjectBanner from './project-banner.vue';

export default {
  name: 'ProjectTile',

  props: {
    size: {
      type: String,
      default: 'md',
      validator(v) {
        return ['xl', 'lg', 'md', 'sm'].includes(v);
      },
    },
    project: {
      type: Object,
      required: true,
    },
  },

  components: {
    ProjectAnchor,
    ProjectBanner,
  },

  data() {
    let startColor = '#abbaab';
    let endColor = '#ffffff';

    if (this.project.theme && this.project.theme.gradient) {
      [startColor, endColor] = this.project.theme.gradient;
    }

    return {
      wrapperStyle: {
        '--gradientStartColor': startColor,
        '--gradientEndColor': endColor,
      },
    };
  },

  computed: {
    classObj() {
      return {
        [`tile--${this.size}`]: true,
      };
    },
    titleStyle() {
      return {
        'margin-bottom': this.size == 'sm' && 0,
      };
    },
    webPFilename() {
      return this.project.bannerImg.match(/(.+)\.(.+)$/i)[1] + '.webp';
    },
  },
};
</script>


<style scoped>
.tile__wrapper {
  padding: 1.2em;
  border-radius: 0.2em;
  border: 1px solid transparent;
  transition: all 0.2s ease-in-out;
}

.tile--lg {
  padding: 1.2em;
}

.tile__wrapper:hover {
  background: rgba(143, 172, 192, 0.2);
}

.tile__description {
  margin: 0;
}

.tile__banner::before {
  content: '';
  height: 100%;
  width: 100%;
  left: 0;
  position: absolute;
  top: 0;
  background: var(--gradientEndColor);
  background: -webkit-linear-gradient(to right, var(--gradientStartColor), var(--gradientEndColor));
  background: linear-gradient(to right, var(--gradientStartColor), var(--gradientEndColor));
  opacity: 0.7;
}
</style>
