<template>
  <div class="tile__banner" :class="classObj">
    <img
      draggable="false"
      class="logo__img"
      :src="require(`@/assets/${logo}`)"
      alt="">

    <picture>
      <source
        draggable="false"
        class="banner__img"
        :srcset="require(`@/assets/${webPFilename}`)"
        type="image/webp"
        alt="">
      <img
        draggable="false"
        class="banner__img"
        :src="require(`@/assets/${banner}`)"
        alt="">
    </picture>
  </div>
</template>


<script>
export default {
  name: 'ProjectBanner',

  props: {
    size: {
      type: String,
      default: 'md',
      validator(v) {
        return ['xl', 'lg', 'md', 'sm'].includes(v);
      },
    },
    logo: {
      type: String,
      default: 'default.svg',
    },
    banner: {
      type: String,
      default: 'default.jpg',
    },
  },

  computed: {
    classObj() {
      return {
        [`banner--${this.size}`]: true,
      };
    },
    webPFilename() {
      return this.banner.match(/(.+)\.(.+)$/i)[1] + '.webp';
    },
  },
};
</script>


<style scoped>
.tile__banner {
  pointer-events: none;
  user-select: none;
  background: rgba(143, 172, 192, 0.2);
  border-radius: 0.2em;
  height: 8em;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.banner--lg {
  height: 10em;
}

.banner--sm {
  height: 5em;
}

.banner--xl {
  height: 16em;
}

.banner__img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.logo__img {
  height: 100%;
  max-height: 3.5em;
  left: 50%;
  position: absolute;
  top: 50%;
  width: 100%;
  max-width: 3.5em;
  transform: translateX(-50%) translateY(-50%);
  filter: drop-shadow(0.1em 0.1em 0.1em rgba(51, 51, 51, 0.4));
}

.banner--lg .logo__img {
  max-height: 5em;
  max-width: 5em;
}

.banner--sm .logo__img {
  max-height: 8em;
  max-width: 8em;
}

.banner--xl .logo__img {
  width: 12em;
}
</style>
